import "core-js/modules/web.url-search-params.delete.js";
import "core-js/modules/web.url-search-params.has.js";
import "core-js/modules/web.url-search-params.size.js";
import WaveSurfer from 'wavesurfer.js';
export default {
  props: {
    audioUrl: {
      type: String,
      required: true
    },
    fileName: {
      type: String,
      required: true
    },
    hasScore: {
      type: Boolean,
      default: true
    },
    scoreDisabled: {
      type: Boolean
    },
    score: {
      type: Number
    }
  },
  data() {
    return {
      wavesurfer: null,
      isPlaying: false,
      duration: 0,
      currentTime: 0,
      value: 0,
      marks: {
        20: 'Bad',
        40: 'Poor',
        60: 'Fair',
        80: 'Good',
        100: 'Excellent'
      }
    };
  },
  mounted() {
    this.initWavesurfer();
  },
  beforeDestroy() {
    this.wavesurfer.destroy();
    URL.revokeObjectURL(this.audioUrl); // 释放内存
  },
  methods: {
    initWavesurfer() {
      this.wavesurfer = WaveSurfer.create({
        container: this.$refs.waveform,
        waveColor: '#718096',
        progressColor: '#4a5568',
        cursorColor: '#2d3748',
        height: 80,
        responsive: true,
        normalize: true
      });

      // 加载音频
      this.wavesurfer.load(this.audioUrl);

      // 事件监听
      this.wavesurfer.on('ready', () => {
        this.duration = this.wavesurfer.getDuration();
      });
      this.wavesurfer.on('audioprocess', time => {
        this.currentTime = time;
      });
      this.wavesurfer.on('finish', () => {
        this.$emit('play-ended'); // 触发播放完成事件
        this.isPlaying = false;
      });
    },
    togglePlay() {
      this.wavesurfer.playPause();
      this.isPlaying = this.wavesurfer.isPlaying();
      if (this.isPlaying) {
        this.$emit('play-start');
      }
    },
    formatTime(seconds) {
      return [Math.floor(seconds / 60), Math.floor(seconds % 60).toString().padStart(2, '0')].join(':');
    },
    handleChange(val) {
      this.$emit('score-change', val, this.fileName);
    }
  }
};